<template>
  <el-container class="main-container">
    <!-- 头部区域 -->
    <el-header>
      <!-- 左侧的 logo -->
      <img src="../../assets/images/logo.png" alt="" />
      <!-- 右侧的菜单 -->
      <el-menu
        class="el-menu-top"
        mode="horizontal"
        background-color="#23262E"
        text-color="#fff"
        active-text-color="#409EFF"
        router
        :default-active="$route.patch">
        <el-submenu index="1">
          <template slot="title">
              <!-- 具名插槽 -->
            <!-- 头像 -->
            <img :src="userInfo.user_pic" alt="" class="avatar" />
            <span>个人中心</span>
          </template>
          <el-menu-item index="/user-info"><i class="el-icon-s-operation"></i>基本资料</el-menu-item>
          <el-menu-item index="/user-avatar"><i class="el-icon-camera"></i>更换头像</el-menu-item>
          <el-menu-item index="/user-pwd"><i class="el-icon-key"></i>重置密码</el-menu-item>
        </el-submenu>
        <el-menu-item @click="logOutHandler"
         ><i index='#' class="el-icon-switch-button"></i>退出</el-menu-item>
      </el-menu>
    </el-header>
    <el-container>
      <!-- 侧边栏区域 -->
      <el-aside width="200px">
        <div class="user-box">
            <img v-if="userInfo.user_pic" :src="userInfo.user_pic" alt="" />
        <span>欢迎: {{userInfo.nickname}}</span>
        </div>
        <el-menu
        :default-active="$route.patch"
        class="el-menu-vertical-demo"
        background-color="#23262E"
        text-color="#fff"
        active-text-color="#409EFF"
        unique-opened
        :router="true"
        >
        <!-- 不包含子菜单的“一级菜单” -->
        <template v-for="item in menus">
            <el-menu-item v-if="!item.children" :key="item.indexPath" :index="item.indexPath"><i :class="item.icon"></i>{{item.title}}</el-menu-item>
        <!-- 包含子菜单的“一级菜单” -->
        <el-submenu v-else :key="item.indexPath" :index="item.indexPath">
            <template #title>
            <i :class="item.icon"></i>
            <span>{{item.title}}</span>
            </template>
            <el-menu-item
            v-for="sub in item.children"
            :key="sub.indexPath"
            :index="sub.indexPath"><i :class="sub.icon"></i>{{sub.title}}</el-menu-item>
        </el-submenu>
        </template>
        </el-menu>
      </el-aside>
      <el-container>
        <!-- 页面主体区域 -->
        <el-main>
          <router-view></router-view>
        </el-main>
        <!-- 底部 footer 区域 -->
        <el-footer>© www.itheima.com - 黑马程序员</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Main',
  data () {
    return {
      menus: []
    }
  },
  created () {
    this.$store.dispatch('getUserInfo')
    this.getMenus()
  },

  methods: {
    logOutHandler () {
      // 注销
      // 清空 Vuex 中的 token
      // 退出提示框
      // this.$confirm 是一个promise 对象，传入成功与失败的回调函数
      this.$confirm('确认退出吗？', '提示').then(
        () => {
          this.$store.commit('updateToken', '')
          this.$message.success('退出成功！！')
          this.$router.push('/login')
        }).catch(e => e)
    },
    async getMenus () {
      const { data: res } = await this.$http.get('/my/menus')
      if (res.code === 0) {
        this.menus = res.data
      }
    }
  },
  computed: {
    ...mapState(['userInfo'])
  }
}
</script>

<style lang="less" scoped>
.main-container {
  height: 100%;
  .el-header,
  .el-aside {
    background-color: #23262e;
  }
  .el-header {
    padding: 0;
    display: flex;
    justify-content: space-between;
  }
  .el-main {
    overflow-y: scroll;
    height: 0;
    background-color: #F2F2F2;
  }
  .el-footer {
    background-color: #eee;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.avatar {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background-color: #fff;
  margin-right: 10px;
  margin-bottom: 2px;
  object-fit: cover;
}
.el-aside {
  .el-submenu,
  .el-menu-item {
    width: 200px;
    user-select: none;
  }
}
.user-box {
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  user-select: none;
  img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
    margin-right: 15px;
    object-fit: cover;
  }
  span {
    color: rgb(255, 255, 255);
    font-size: 12px;
  }
}
</style>
